
#history{
        overflow-x: hidden;
        background: #17233b;
        background: linear-gradient(90deg, #0a0f19, #17233b);
        height: 100vh;
        color: #ddd;
}
.hty_heading{
        background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(#bdbfc2), to(#ffffff));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        margin: 0;
        text-align: center;
        font-size: 2rem;
        line-height: 2rem;
        width: 100vw;
}
.hty_sub_heading{
        font-size: 1em;
        margin: 0 1em .2em 0;
        text-align: right;
}
.hty_sub_nav {
        position: relative;
        padding: 1em 0;
        font-size: 1.3rem;
        line-height: 1.3rem;
        text-align: center;
        width: 100vw;
}
.hty_more{
        font-size: 2rem;
        line-height: 2rem;
        position: absolute;
        left: .5em;
        font-weight: bolder;
        text-decoration: none;
        color: aliceblue;
        z-index: 3;
}
.hty_rec{
        -ms-flex: 0 0 0 40%;
            flex: 0 0 0 40%;
        display: -ms-flexbox;
        display: flex;
        margin: 1em 0;
}
.hty_header{
        -ms-flex: 2;
            flex: 2;
}
.hty {
        background: rgb(16,24,45);
        margin: 1% 0;
        padding: 0.7em 0;
        text-align: center;
}
.hty_con_status {
        width: 23%;
        display: inline-block;
        text-align: center;
        border-left: .1em solid;
}
.hty_con_header {
        display: block;
        font-size: 0.7em;
}
.hty_name{
        font-size: .8em;
        color: aliceblue;
}
.hty_con_num_show {
        font-size: 1em;
        color: aliceblue;
}
.hty_con_num_show:after{
        content: 'ug/cm^3';
        font-size: .7em;
}



