
#manager{
	color: white;
}
.one_tea{
	overflow: hidden;
	width: 100%;
	padding: 10px 0;
	border-bottom: 1px solid white;
}
.tea_id, .tea_name, .tea_tele, .add_tea{
	width: 24%;
	display: inline-block;
}
.lg_header{
    margin: 0;
    padding: 0;
    font-weight: 500;
    text-align: center;
    font-size: 2.3em;
    color: #3b73de;
    display: inline-block;
    font-family: 'Microsoft YaHei';
    width: 100%;
    background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(#3b73de), to(#3ec2e5));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.not_tea{
	text-align: center;
    margin: 20px 0;
}
.tea_add_bn{
    color: white;
    background-color: #3b73de;
    border: none;
    border-radius: 20px;
    padding: 3px 20px;
}
.send_add_tea{
	clear: both;
    margin: 20px auto;
    display: block;
}
.tea_add_bn:hover,.send_add_tea:hover{
	background: #3ec2e5;
}
.tea_name_text{
	text-indent: 10px;
}
.tea_change_link{
	color: white;
	text-decoration: none;
}
.tea_change_link:hover{
	color: #3b73de;
}
.tea_bn{
	display: inline-block;
	cursor: pointer;
    padding: 0px 3px;
}
.tea_bn:hover{
	color: #3b73de;
}



