
html {
        min-height: 100%;
}
#app{
        width: 100%;
}
body {
        margin: 0;
        padding: 0;
        font-family: '微软雅黑';
        font-size: 1em;
        min-height: 100%;
}
#see_login{
        overflow-x: hidden;
        padding: .5em 1.4em;
}
#tea_msg{
        background: #070c14;
        background: linear-gradient(#070c14 ,#0a101b,#070c14);
        text-align: center;
        width: 100%;
        color: white;
        padding: 2% 0;
        font-size: 1em;
}
.tea_name_sub_heading{
        padding: 0;
        margin: 0;
        font-weight: normal;
        font-size: 1.2em;
}
.tea_num{
        padding: 0;
        margin: 0;
        font-weight: normal;
        color: #ddd;
        font-size: 1em;
}
.rooms{
        overflow-x: hidden;
        background: #17233b;
        background: linear-gradient(90deg, #0a0f19 ,#17233b);
        height: 100vh;
}
.one_room{
        float: left;
        width: 44%;
        box-sizing: border-box;
        background: #22406F;
        padding: 0% 0;
        margin: 7% 1.5% 0 3%;
        text-align: center;
        border-radius: 1.3em;
}
.point{
        width: 1em;
        height: 1em;
        margin: .1em auto;
        border-radius: 50%;
}
.color_normal .point{
        background-color: #04b243;
}
.color_warn .point{
        background-color: red;
}
.room_link{
        display: block;
        color: #777;
        text-decoration: none;
        height: 10em;
}
.room_warn{
        box-shadow: 0 0 1em 0.3em red;
}
.room_name{
        color: white;
}
.room_status{
        color: rgb(4, 178, 67);
        font-size: 1.6em;
        font-weight: bolder;
}
.room_link_a{
        text-decoration: none;
        display: block;
        overflow: hidden;
        padding: 36% 0 0;
}
.color_warn{
        color: red;
}



